<template>
  <body id="paper">
    <el-form :model="loginForm" :rules="rules" class="login-container" label-position="left"
             label-width="0px" v-loading="loading">
      <h3 class="login_title">登录</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="loginForm.username"
                  auto-complete="off" placeholder="邮箱"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password"
                  auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 40%;background: #505458;border: none" v-on:click="login">登录</el-button>

        <a href="/register"><router-link to="register"><el-button type="primary" style="width: 40%;background: #505458;border: none">注册</el-button></router-link></a>
      </el-form-item>
    </el-form>
  </body>
</template>
<script>
  const axios = require('axios')
  export default{
    props: ['user'],
    data () {
      return {
        rules: {
          username: [{required: true, message: '邮箱不能为空', trigger: 'blur'}],
          password: [{required: true, message: '密码不能为空', trigger: 'blur'}]
        },
        checked: true,
        loginForm: {
          username: '',
          password: ''
        },
        loading: false
      }
    },
    methods: {
      login () {
        if (this.user.uid) {
          this.$alert('已有用户登录', '提示', {
            confirmButtonText: '确定'
          })
          return
        }
        var _this = this
        axios({
            method: 'post',
            url: '/login',
            data: {'mail': this.loginForm.username, 'password': this.loginForm.password},
            header: {
              // 如果写成contentType会报错
              'Content-Type': 'application/json'
            }
          }).then(resp => {
           if (resp.data.uid === '') {
             this.$alert('登录失败！请检查是否输入错误！', '提示', {
               confirmButtonText: '确定'
             })
           } else {
             this.$alert(resp.data.uid, '提示', {
               confirmButtonText: '确定'
             })
             this.user.uid = resp.data.uid
             this.$emit('change',this.user)
             _this.$router.replace('/index')
           }

          })
          .catch(failResponse => {})
      }
    }
  }
</script>
<style>
   body{
     margin: 0;
   }
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }
</style>



